import React from 'react';

/*
**
Autor: Samuel Sepei
Dtum: 10.5.2018
Komponent: TableColumns
**
*/

/*
Komponent, ktor sa star o poet stpcov v tabuke. Vstupn pole je typu kla.
*/

/*
Po zmene potu stpcov vol rodiovsk funkciu s aktualizovanou hodnotou stpcou. 
Rodi si hodnotu ulo, zv alebo zmen tabuku a vygeneruje nov LaTeX kd.
*/

/*  global localStorage: false, console: false, */

class TableColumns extends React.PureComponent {
  static getInitialColumnsState() {
    let columns = 5;
    if (localStorage.getItem('table-columns') !== null) {
      columns = (localStorage.getItem('table-columns') - 1) / 2;
    }
    return columns;
  }

  constructor(props) {
    super(props);
    this.state = {
      columns: TableColumns.getInitialColumnsState(),
    };

    this.onChange = this.onChange.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.columns !== this.state.columns) {
      this.setState({ columns: nextProps.columns });
    }
  }

  onChange(event) {
    if (event.target.value < 1) {
      this.setState({
        columns: 1,
      });
      this.props.columnValue(1);
    } else if (event.target.value > 15) {
      this.setState({
        columns: 15,
      });
      this.props.columnValue(15);
    } else {
      this.setState({
        columns: event.target.value,
      });
      this.props.columnValue(event.target.value);
    }
  }

  render() {
    return (
      <div className=" table-column-size">
        <label htmlFor="table-columns">
          Columns: {this.state.columns}
          <input
            id="table-columns"
            type="range"
            min="1"
            max="15"
            value={this.state.columns}
            onChange={this.onChange}
          />
        </label>
      </div>
    );
  }
}

export default TableColumns;



